.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #282c34;
  min-height: 100vh;
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-body {
  background-color: #282c34;
  font-size: calc(8px + 1vmin);
  color: white;
  overflow: hidden;
  width: 50vw;
}

.App-footer {
  background-color: #282c34;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
  color: white;
  overflow: hidden;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin-y {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(360deg);
  }
}

.Table-header {
  color: #61dafb;
}

.Button-styled {
  background-color: black;
  color: white;
  font-size: 20px;
  padding: 10px 60px;
  border-radius: 5px;
  margin: 10px 0px;
  cursor: pointer;
}

.Button-styled:hover {
  box-shadow: 0 0 10px cyan;
}

.Button-styled:disabled {
  opacity: 0.4;
}

.Button-styleda:active {
  box-shadow: 0 0 10px yellow;
}

.Button-styled:hover:disabled {
  box-shadow: 0 0 10px red;
  opacity: 0.4;
}

.Table-label {
  color: #61dafb;
  font-size: medium;
}

.top-left {
  position: absolute;
  top: -0;
  left: 115px;
  font-size: 30px;
  font-weight: normal;
  line-height: 1.5;
  font: "Roboto"; 
  font-stretch: condensed;
}

.top-right {
  position: absolute;
  top: -0;
  left: 375px;
  font-size: 30px;
  color: gold;
  font-weight: normal;
  line-height: 1.5;
  font: "Roboto"; 
  font-stretch: condensed;
}

.emblem {
  height: 100px;
  width: 475px;
}

.table-styled {
  border-collapse: collapse;
  background: #282c34;
  border-radius: 10px;
  overflow: hidden;
  width: 75%;
  margin: 0 auto;
  position: relative;
}

.TextField{
  background:#fdffff;

}